const searchField = document.querySelector('.js-search-header');
const searchFieldInput = document.querySelector('.js-search-header__input');
const searchFieldBtn = document.querySelector('.js-search-header__btn');

searchFieldBtn.addEventListener('click', function(e) {
	searchField.classList.add('active');
});

document.body.addEventListener('click', function(e) {
	if (!e.target.closest('.js-search-header')) {
		searchField.classList.remove('active');
		searchFieldInput.value = '';
	}
});