import { lazyLoadJs } from "../lazyloadjs";

const runInitInputMask = function() {
	let elements = document.querySelectorAll('.js-phone-mask');

	if (elements.length) {
		lazyLoadJs({
			targets: elements,
			options: {
				callback: function() {
					import(
						/* webpackChunkName: "inputmask" */
						'inputmask/lib/inputmask'
					).then((module) => {
						let inputmask = module.default;

						initInputMask(inputmask);
					});
				}
			}
		});
	}

	function initInputMask(Inputmask) {
		let im = new Inputmask('+7 999 999-99-99', {
			"clearIncomplete": true
		});
	
		im.mask(elements);
	}
};

runInitInputMask();

// Add an event listener
document.addEventListener("popup:shown", function(e) {
	runInitInputMask();
});