var checkSupportWebp = function(fn) {
	var WebP = new Image();
	
 	WebP.onload = WebP.onerror = function() {

 		isSupported = (WebP.height === 2);

 		fn(isSupported);
 	};
	
 	WebP.src = 'data:image/webp;base64,UklGRjoAAABXRUJQVlA4IC4AAACyAgCdASoCAAIALmk0mk0iIiIiIgBoSygABc6WWgAA/veff/0PP8bA//LwYAAA';
};

checkSupportWebp(function(isSupportedWebp){
	if (isSupportedWebp) {
		document.addEventListener('lazybeforeunveil', function(e){
			var bg = e.target.getAttribute('data-bg');
 			var bgWebp = e.target.getAttribute('data-bg-webp');
			
 			if (bgWebp) {
 				e.target.style.backgroundImage = 'url(' + bgWebp + ')';
 			} else if (bg) { // если есть подержка webp но нет атрибута data-webp
				e.target.style.backgroundImage = 'url(' + bg + ')';
			}
		});
	} else {
		document.addEventListener('lazybeforeunveil', function(e){
			var bg = e.target.getAttribute('data-bg');

			if (bg) {
				e.target.style.backgroundImage = 'url(' + bg + ')';		
			}
		}, false);
	}

	var timerId;
	timerId = setTimeout(function(){
		if (window.lazySizes) {
			clearTimeout(timerId);
			window.lazySizes.init(); // include init lazysizes
		}
	});
});