const defaultOptions = {
	root: null,
    rootMargin: "20% 0px 20% 0px",
    threshold: 0,
	triggerOnce: true
};

const extend = function(options) {
	return {...defaultOptions, ...options}
};

const isNodeList = function(nodes) {
	return NodeList.prototype.isPrototypeOf(nodes);
};

const convertTargetsToArray = function(nodes) {
	if (isNodeList(nodes)) {
		return Array.from(nodes); // if used for example document.querySelectorAll('el')
	} else {
		return [nodes]; // if used for example document.querySelector('el')
	}
};

export function lazyLoadJs(config) {
	const options = extend(config.options);
	const targets = convertTargetsToArray(config.targets);

	const observer = new IntersectionObserver(function(entries, observer) {
		entries.some(entry => {
			if (entry.isIntersecting) {
				// console.log('execute');
				options.callback();

				if (options.triggerOnce) {
					observer.disconnect();

					return true; // break loop
				}
			}
		});
	}, options);

	targets.forEach(function(target) {
		observer.observe(target);
	});
}