import { lazyLoadJs } from "../../js/lazyloadjs";

const header = document.querySelector('.js-header');
const isPhone = window.matchMedia("(max-width: 767px)").matches ? true : false;

// Move map in phone to first tab block
if (isPhone) {
	const map = document.querySelector('#map');
	const mobMap = document.querySelector('.js-adresses-block__mob-map');

	if (mobMap) {
		mobMap.appendChild(map);
	}
}
// end

// Functional of show hide dropdown
let titles = document.querySelectorAll('.js-adresses-block__title');

if (titles.length) {
	let arrTitles = Array.from(titles);

	arrTitles.forEach(function(title, index) {
		title.addEventListener('click', function() {
			const parent = title.parentNode;
			
			parent.classList.toggle('active');
			
			arrTitles.forEach((title, index) => {
				if (title !== this) {
					title.parentNode.classList.remove('active');
				}
			});

			// Scroll to active city
			if (isPhone && parent.classList.contains('active')) {
				const offsetTop = parent.getBoundingClientRect().top + document.documentElement.scrollTop;
				
				document.documentElement.scrollTop =  offsetTop - header.clientHeight - 30;
			}
			// end

			// Move map in phone to active tab block
			if (isPhone) {
				const map = document.querySelector('#map');
				const mobMap = parent.querySelector('.js-adresses-block__mob-map');

				if (mobMap) {
					mobMap.appendChild(map);
				}
			}
			// end
		});
	});
}
// end

// Lazy load of map
const adressesBlock = document.querySelector('.js-adresses-block');

if (adressesBlock) {
	lazyLoadJs({
		targets: adressesBlock,
		options: {
			callback: function() {
				loadMap();
			}
		}
	});
}

function loadMap() {
	const script = document.createElement("script");
	script.type = "text/javascript";
	script.async = true;
	script.src = "https://api-maps.yandex.ru/2.1/?lang=ru_RU&amp;apikey=9fb45093-1306-4dc4-a928-84873cbf6687";
	script.onload = function() {
		initMap(); // Call by function because (ymaps) variable not exist yet
	};

	document.body.appendChild(script);
}
// end

// Functional of Map
function initMap() {
	// Функция ymaps.ready() будет вызвана, когда
	// загрузятся все компоненты API, а также когда будет готово DOM-дерево.
	ymaps.ready(init);

	function init(){
		const cities = document.querySelectorAll('.js-adresses-block__title');
		const initialCordinates = cities[0].dataset.cordinatesOfMap.split(",");

		// Создание карты.
		const map = new ymaps.Map("map", {
			center: initialCordinates,
			zoom: 14,
			controls: []
		});

		// placemark icon
		const myPlacemark = new ymaps.Placemark(map.getCenter(), {}, {
			iconLayout: 'default#image',
			iconImageHref: 'images/icons/map-pin.svg',
			iconImageSize: [35, 50],
			iconImageOffset: [0, -50]
		});

		map.geoObjects.add(myPlacemark);
		// end

		// buttons for switch schema of map
		const ButtonLayout = ymaps.templateLayoutFactory.createClass([
			'<div class="map-custom-buttons">',
				'<span class="map-custom-buttons__btn active js-map-custom-buttons__btn" data-map-type="yandex#map">$[data.firstButtonText]</span>',
				'<span class="map-custom-buttons__btn js-map-custom-buttons__btn" data-map-type="yandex#satellite">$[data.secondButtonText]</span>',
			'</div>',
		].join(''));

		const button = new ymaps.control.Button({
			data: {
				firstButtonText: "TFS Map",
				secondButtonText: "Спутник"
			},
			options: {
				layout: ButtonLayout
			}
		});

		map.controls.add(button);

		button.events.add('click', function (e) {
			const buttons = document.querySelectorAll('.js-map-custom-buttons__btn');

			Array.from(buttons).forEach(function(btn) {
				if (e.get('domEvent').get('target') === btn) {
					const mapType = btn.dataset.mapType;

					if (mapType) map.setType(mapType);

					btn.classList.add('active');
					return;
				}

				btn.classList.remove('active');
			});
		});
		// end

		// Change cordinates after clicked by address
		Array.from(cities).forEach(function(city) {
			city.addEventListener('click', function() {
				const cordinate = city.dataset.cordinatesOfMap.split(",");

				map.setCenter(cordinate);
				myPlacemark.geometry.setCoordinates(cordinate);
			});
		});
	}
}
// end
